import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { YearService, ClassService, HomeService } from "@core/services/serviceIndex";
import { forkJoin } from 'rxjs';

@Component({
  selector: 'app-assessment-create',
  templateUrl: './assessment-create.component.html',
  styleUrls: ['./assessment-create.component.css']
})
export class AssessmentCreateComponent implements OnInit {
  public assessmentForm: FormGroup;
  public classLists: any = [];
  public direction: String;
  public yearList: any = [];
  public page: any = 1;
  public submitted: boolean = false;
  public search: String;
  public isModal: boolean = false;
  public selectedClass: any;

  @Output() onSave: any = new EventEmitter<any>()
  @Input() assessmentId: any;

  constructor(private formBuilder: FormBuilder, private _yearService: YearService,
    private _classService: ClassService, private _hservice: HomeService) { }

  ngOnInit(): void {
    this.assessmentForm = this.formBuilder.group({
      title: ["", [Validators.required]],
      year: ["", [Validators.required]],
      description: [""],
    });

    this.getLists();
  }

  getLists() {
    // results[0] is our api1
    // results[1] is our api2
    // results[1] is our api3
    let api1 = this._yearService.getYearList();
    let api2 = this._classService.classLists(this.search, this.direction, this.page);
    if (this.assessmentId) {
      let api3 = this._hservice.getAssessmentDetail(this.assessmentId);
      forkJoin([api1, api2, api3]).subscribe(results => {
        this.isModal = true;
        this.yearList = results[0].years;
        this.classLists = results[1].classes;
        let assessment = results[2].assement;
        this.assessmentForm = this.formBuilder.group({
          title: [assessment.title, [Validators.required]],
          year: [assessment.year.yearId, [Validators.required]],
          description: [assessment.description],
        });
      });
    } else {

      forkJoin([api1, api2]).subscribe(results => {
        this.isModal = true;
        // results[0] is our api1
        // results[1] is our api2
        this.yearList = results[0].years;
        this.classLists = results[1].classes;
      });
    }
  }

  onSubmit() {
    this.submitted = true
    console.log(this.assessmentForm.value, "<<<>>")
    if (this.assessmentForm.invalid) return;
    else {
      this.isModal = false;
      let tempData: any = {};
      tempData.title = this.assessmentForm.value.title;
      tempData.year = this.assessmentForm.value.year;
      tempData.description = this.assessmentForm.value.description;
      if (this.assessmentId)
        this.onUpdate(tempData);
      else this.onCreate(tempData);
    }
  }

  onCreate(tempData: any) {
    this._hservice.createAssessment(tempData)
      .subscribe((res) => {
        this.onClick();
      })
  }

  onUpdate(tempData: any) {
    tempData.assessmentId=this.assessmentId;
    this._hservice.updateAssessment(tempData)
      .subscribe((res) => {
        this.onClick();

      })
  }

  // convenience getter for easy access to form fields
  get f() { return this.assessmentForm.controls; }

  onClick() {
    this.isModal = false;
    let temp = {
      close: false,
    }
    this.onSave.emit(temp);
  }

  getclass(data: any) {
    console.log(data);
    this.selectedClass = data;
  }
}
