import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from "@angular/forms";

// com lists
import { HomeComponent } from './home/home.component';
import { AdminHomeComponent } from "./admin-home/admin-home.component";
import { TeacherHomeComponent } from './teacher-home/teacher-home.component';
import { ParentHomeComponent } from './parent-home/parent-home.component';
import { StudentHomeComponent } from './student-home/student-home.component';
import { TempUIComponent } from './temp-ui/temp-ui.component';
import { AssessmentScoreCreateComponent } from './assessment-score-create/assessment-score-create.component';
import { DropDownSearchComponent } from './drop-down-search/drop-down-search.component';
import { AssessmentCreateComponent } from './assessment-create/assessment-create.component';

// other
import { Role } from "@enum/app_enum";
import { AuthGuardService } from "@core/guards/auth-guard.service";
import { BasicComponentsModule } from "@basic-components/basic-components.module";



let comLists = [
  HomeComponent,
  TeacherHomeComponent,
  AdminHomeComponent,
  ParentHomeComponent,
  StudentHomeComponent,
  TempUIComponent,
  AssessmentScoreCreateComponent,
  DropDownSearchComponent,
  AssessmentCreateComponent,
];

const routes: Routes = [
  {
    path: 'admin-home',
    component: AdminHomeComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Admin] }
  },
  {
    path: 'student affair-home',
    component: AdminHomeComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.StudentAffair] }
  },
  {
    path: 'teacher-home',
    component: TeacherHomeComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Teacher] }
  },
  {
    path: 'parent-home',
    component: ParentHomeComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Parent] }
  },
  {
    path: 'student-home',
    component: StudentHomeComponent,
    canActivate: [AuthGuardService],
    data: { roles: [Role.Student] }
  },
  {
    path: 'temp-uiview',
    component: TempUIComponent,
  },
];



@NgModule({
  declarations: [comLists,],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    BasicComponentsModule,
    ReactiveFormsModule,
    FormsModule
  ]
})
export class HomeModule { }
