import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { UserExpands,Role } from "@enum/app_enum";
@Component({
  selector: 'app-temp-ui',
  templateUrl: './temp-ui.component.html',
  styleUrls: ['./temp-ui.component.css']
})
export class TempUIComponent implements OnInit {
  public tempForm: FormGroup;
  public submitted=false;
  public userRole=[Role.Admin,Role.Parent,Role.Student,Role.Teacher,Role.StudentAffair];
  constructor(private formBuilder: FormBuilder,) { }

  ngOnInit(): void {
    this.tempForm = this.formBuilder.group({
      date: ["", [Validators.required]],
      gender: ["male", [Validators.required]],
      userRole: ["", [Validators.required]],
      time: ["", [Validators.required]],
      
    });
    
  }

  onSubmit() {
    this.submitted = true;

    // stop here if form is invalid
    if (this.tempForm.invalid) {
      return;
    }


  }
  // convenience getter for easy access to form fields
  get f() { return this.tempForm.controls; }

 
   // Choose city using select dropdown
   changeRole(e) {
    this.role.setValue(e.target.value, {
      onlySelf: true
    })
  }

  // Getter method to access formcontrols
  get role() {
    return this.tempForm.get('userRole');
  }
}
