import { Component, OnInit } from '@angular/core';
import { ActionLabel } from "@enum/app_enum";
import { HomeService } from "@core/services/serviceIndex";
@Component({
  selector: 'app-parent-home',
  templateUrl: './parent-home.component.html',
  styleUrls: ['./parent-home.component.css']
})
export class ParentHomeComponent implements OnInit {
  public tabLists: any = [ActionLabel.attandance, ActionLabel.paymentInfo];
  public activeIndex: any = 0;
  public paymentList: any = [];
  public attandenceList: any = [];
  public isLoading:boolean=false;

  constructor(private _homeser:HomeService) { }

  ngOnInit(): void {
    this.getData();
  }

  ontagChange(e: any) {
    this.activeIndex = e;
  }


  getData() {
    this.isLoading=true;
    this._homeser.getParentHome()
    .subscribe((res)=>{
      console.log(res);
      this.paymentList=res.payments;
      this.attandenceList=res.attendance;
      this.isLoading=false;
    })

  }
}
