import { Component, Input, OnInit,Output,EventEmitter } from '@angular/core';

@Component({
  selector: 'app-drop-down-search',
  templateUrl: './drop-down-search.component.html',
  styleUrls: ['./drop-down-search.component.css']
})
export class DropDownSearchComponent implements OnInit {
  @Input() ddIndex:any;
  @Input() lists:any;
  @Output() selected: any = new EventEmitter<any>();
  @Input() selectedList:any;
  public isShow:boolean=false;
  
  constructor() { }

  ngOnInit(): void {
  }

  open(){
    this.isShow=!this.isShow;
  }

  onselectList(list){
    console.log(list);
    list.ddIndex=this.ddIndex;
    console.log(list)
    this.selected.emit(list);
    this.selectedList=list;
    this.isShow=false;
  }
}
